$bgW: #fff;
body{
	padding-bottom:60px;
}
.free-static-app{
	position: fixed;
	margin:0 20px;
	bottom:70px;
	left:50%;
    z-index: 9999;
	margin-left:-140px;
	display:-webkit-box;
	display:-moz-box;
	display:box;
	width:280px;
	height:40px;
	line-height:40px;
	color:$bgW;
	background: #10c1ae;
	border-radius:35px;
	dt{
		-webkit-box-flex:1;
		-moz-box-flex:1;
		box-flex:1;
		font-size:14px;
        span{
        	display:inline-block;
        	margin:0 10px;
			width:8px;
			height:8px;
			background:url(../../img/wx/laikan/icon/lkIndex-other.png?v=6) no-repeat;
	        background-position:-32px -28px;
	        -webkit-background-size: 50px auto;
	        background-size: 50px auto; 
	        vertical-align: 2px;
        }
	}
	dd{
		margin:8px 15px 0 0;
		width:60px;
		height:22px;
		line-height:22px;
		text-align:center;
		border:solid 1px #fff;
		border-radius: 4px;
		a{
			display:block;
			color:#fff;
			font-size:12px;
		}
	}
}
.freeType{
	display:-webkit-box;
	display:-moz-box;
	display:box;
	background: $bgW;
	li{
		position: relative;
		-webkit-box-flex:1;
		-moz-box-flex:1;
		box-flex:1;
		height:40px;
		line-height:40px;
		font-size:1.6rem;
		color:#999;
	    text-align: center;
    	border-bottom: solid 1px #ddd;
    	span{
    		position: absolute;
    		left:50%;
    		bottom:-1px;
		    margin-left: -23px;
		    width: 46px;
		    height: 2px;  
			border-bottom:solid 2px transparent;
    	}
    	&.on{
			color:#10c1ae;
			font-weight:bold;
			span{
				border-bottom:solid 2px #10c1ae;
			}
    	}
	}
}
.boy{
	display:none;
}
.ul-mod li span.name{
	height:32px !important;
}